.container2{
	font-size: 25px;
	box-sizing: border-box;	
	font-family: 'Redressed', cursive;
	color: white;
	height: 100%;
	width: 100%;
	background-image: linear-gradient(to right,#ff6666,#3399ff);
	padding: 1%;
	margin: 0%;
}
.head{
	color:white;
	padding: 1%;
	margin: 0%;
}
.head h3{
	font-weight: 400;
	font-size: 35px;
}
.form{
	width: 70%;
	margin-left: 15%;
	border: 2px solid white;
	padding: 1%;
	margin-bottom: 2%;
}
@media screen and (max-width: 600px){
	.container2{
		font-size: 20px;
		box-sizing: border-box;	
		font-family: 'Redressed', cursive;
		color: white;
		height: 100%;
		width: 100%;
		background-image: linear-gradient(to right,#ff6666,#3399ff);
		padding: 5%;
		margin: 0%;
	}
	.head{
		color:white;
		padding: 2%;
		margin: 0%;
	}
	.head h3{
		font-weight: 400;
		font-size: 30px;
	}
	.form{
		width: 95%;
		margin-left: 2.5%;
		border: 2px solid white;
		padding: 2%;
		margin-bottom: 5%;
	}
}